import React, { createElement, useState } from 'react'
import Measure from 'react-measure'

import isIE from '../components/utils/isIE'

const ChartSize = ({ Comp, width, ...props }) => {
  const [size, setSize] = useState({})
  return isIE ? (
    <Measure bounds onResize={({ bounds }) => setSize(bounds)}>
      {({ measureRef }) => (
        <div ref={measureRef}>
          {size.width && <Comp {...props} key={size.width} isIE size={size} ratio={size.width / width} />}
        </div>
      )}
    </Measure>
  ) : createElement(Comp, { ...props, size: { width }, ratio: 1 })
}

export default width => Comp => props => createElement(ChartSize, { ...props, Comp, width })
